  background-color: #e7e7fd;
} */

.light:hover {
  background-color: #bfbfbf;
}

.dark{
  background-color: #bfbfbf;
}

select {
  cursor: pointer;
  position: relative;
  padding-left: 1.356vh;
  font-family: ITCFranklinGothicStd-Med;
  font-size: 1.877vh;
  letter-spacing: 0.7px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;       /* Remove default arrow */
  background: url('https://upload.wikimedia.org/wikipedia/commons/4/42/Font_Awesome_5_solid_caret-down.svg') no-repeat white;
  /* background-attachment: fixed; */
  background-position: bottom 1.043vh right 1.147vh; 
  background-size: 2.086vh;
}

select.option{
 cursor: pointer;
}

select:disabled {
  cursor: default;
}


/* Responsiveness for Mobile Friendly */

@media only screen and (max-width: 1520px) {
  #tabular-data-2 {
    float: none !important;
  }
}


